	module.exports = "<div id=\"help\"><h2>help-component</h2><p>そのうちサービスのhelpページになります</p><p>いまはbootstrapが使えることを示すサンプルがあります.</p><h3>Bootstrapのグリッドシステムを使ってみるよ</h3><p>適当にbootstrapを使ってみますよ? index.pugのクラス指定を見てみてグリッドシステムのクラスが入ってるでしょう？</p><p>こんな感じに使っていくのだ,それにしてもpugのクラス指定のかっこよさよ＞＿＜</p><div class=\"row\"><div class=\"blue col-md-4\">col-md-4</div><div class=\"red col-md-6\">col-md-6</div><div class=\"yellow col-md-2\">col-md-2</div></div><h3>次にbootstrapのようなUIコンポーネントを使用する例を示すよ</h3><p>普通にbootstrapでボタンとか使おうとしたらjqueryとbootstrap.jsを呼ぶよね？あれは中でDOMを操作していい感じにやってくれるんだ</p><p>でも今回のアプリはDOMを勝手にいじってほしくないよ JSフレームワークの考え方とDOMを直接操作して書き換えたりすることは相反するからね　</p><p>もしそんなことをしたらパフォーマンスの低下や不明なバグに悩まされる可能性が高いよ</p><p>そこで今回はVueJSをベースに作られたbootstrapのコンポーネント（ボタンとか日付ピッカーとかタブとかアコーディオンとかモーダルとか・・・）を実現するvue-strapっていうのを使うよ</p><h4>アラートを出してみる</h4><p>コンポーネントはいっぱいあるけどアラートを出してみる例を示すよ</p><button class=\"btn btn-danger btn-lg\" @click=\"showTop = !showTop\">click to toggle alert on top</button><alert :show.sync=\"showTop\" placement=\"top\" duration=\"3000\" type=\"danger\" width=\"400px\" dissmissable=\"dissmissable\"><span class=\"icon-info-circled alert-icon-float-left\"></span><strong>Heads up!</strong><p>This alert needs your attention.</p></alert><p>ボタン押すとアラートが出たと思うよ！ コードを読むとほとんど何も書いてないのにこれだけのことができたことがわかるよ</p><h4>ほかのUIコンポーネントは</h4><a href=\"https://yuche.github.io/vue-strap/\">vue-strap</a><p>今のアラートはこのサイトの丸パクリだよ　pugに書き直しはしたけどね. こんな感じでどんどん使っていこう</p><p>bs-input</p><bs-input label=\"Textarea\" type=\"textarea\"></bs-input><h3>Font-awesome</h3><p>font-awesomeのアイコンが使えるよ　クエスチョンマークとgithubアイコン出すよ</p><div class=\"text-center\"><i class=\"fa fa-4x fa-question\"></i><i class=\"fa fa-4x fa-github\"></i></div></div>";

